import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { DataService } from '../services/data.service';
import { NavController, ActionSheetController, AlertController, ModalController } from '@ionic/angular';
import { LoadingService } from '../services/loading.service';

import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { AngularFirestore } from '@angular/fire/firestore';

@Component({
  selector: 'app-feed',
  templateUrl: './feed.page.html',
  styleUrls: ['./feed.page.scss'],
})
export class FeedPage implements OnInit {
  unreadMessagesCount: any; 
  friendRequestCount: any;
  unreadGroupMessagesCount: any;
  groupList: any;
  groupsInfo: any;
  conversationList: any;
  conversationsInfo: any; 

  private title: any;
  private groupId: any;
  private group: any;
  private groupMembers: any;
  private loggedInUserIsMember: any = 'false';
  private posts: any = [];

  constructor(    
    public dataProvider: DataService,
    public navCtrl: NavController,
    public modalCtrl: ModalController,
    public firestore: AngularFirestore,
    public alertCtrl: AlertController,
    public loadingProvider: LoadingService,
    public actionSheet: ActionSheetController,
    private route: ActivatedRoute,
    private router: Router,
    ) { }

  ngOnInit() {
  }

  ionViewDidEnter() {

    // TODO: remove this hardcoded groupid
    this.groupId = 'SWSn0dRwU3IksNSMGrvn';
    // Get friend requests count.
    this.dataProvider.getRequests(this.dataProvider.getCurrentUserId()).get().then((requestsRes: any) => {
      const requests = requestsRes.data;
      if (requests != null) {
        if (requests.friendRequests != null) {
          this.friendRequestCount = requests.friendRequests.length;
        } else { this.friendRequestCount = null; }
      } else { this.friendRequestCount = null; }
    });

    // Get conversations and add/update if the conversation exists, otherwise delete from list.
    this.dataProvider.getConversations().get().subscribe((conversationsInfoRes: any) => {
      if (conversationsInfoRes.length > 0) {
        conversationsInfoRes.forEach((conversationInfo) => {
          if (conversationInfo.blocked != true) {
            this.dataProvider.getConversation(conversationInfo.conversationId).snapshotChanges().subscribe((conversationRes: any) => {
              if (conversationRes.payload.exists) {
                let conversation: any = { $key: conversationRes.key, ...conversationRes.payload.data() };
                if (conversation.blocked != true) {
                  this.addOrUpdateConversation(conversation);
                }
              }
            });
          }
        });
      }
    });
  
   // Get Posts
           // Get group posts
    this.firestore.collection('/posts/').ref
           // .where('groupId', '==', this.groupId)
           .get().then((snapshot) => {
             console.log('where clause' + snapshot);
             snapshot.forEach((childSnapshot) => {
               let post: any;
               post = { key: childSnapshot.id, ...childSnapshot.data() };
               const startDate = new Date(post.date);
               // Do your operations
               const endDate   = new Date();
               const seconds = (endDate.getTime() - startDate.getTime()) / 1000;
               if (seconds > 120) {
                 post.showNewIcon = false;
               } else {
                 post.showNewIcon = true;
               }
   
               // Check for Thanks
               let totalReactionCount = 0;
               let totalReviewCount = 0;
               if (post.reviews !== undefined) {
                 const rev = Object.keys(post.reviews).map(function(e) {
                   totalReviewCount += 1;
               });
               }
   
               let foundSmiley = false;
               if (post.reactions !== undefined) {
                 const values = Object.keys(post.reactions).map(function(e) {
                   post.reactions[e].key = e;
                   totalReactionCount += 1;
                   return post.reactions[e];
                 });
                 foundSmiley = values.some(el => el.addedByUser.addedByKey === this.dataProvider.getCurrentUserId() && el.reactionType === 'Thanks');
               }
               if (foundSmiley) {
                 post.showSmiley = true;
               } else {
                 post.showSmiley = false;
               }
               // Check for Hugs
               let foundHug = false;
               if (post.reactions !== undefined) {
                 const values = Object.keys(post.reactions).map(function(e) {
                   post.reactions[e].key = e;
                   return post.reactions[e];
                 });
                 foundHug = values.some(el => el.addedByUser.addedByKey === this.dataProvider.getCurrentUserId() && el.reactionType === 'Hug');
               }
               if (foundHug) {
                 post.showHug = true;
               } else {
                 post.showHug = false;
               }
   
               post.totalReactionCount = totalReactionCount;
               post.totalReviewCount = totalReviewCount;
               this.addOrUpdatePost(post);
               // this.dataProvider.getUser(childData.sender).snapshotChanges().subscribe((user: any) => {
               //   childData.avatar = user.payload.data().img;
               // });
               // this.posts.push(childData);
             });
             });
  }

  addOrUpdatePost(post) {
    if (!this.posts) {
      this.posts = [post];
    } else {
      let index = -1;
      for (let i = 0; i < this.posts.length; i++) {
        if (this.posts[i].key == post.key) {
          index = i;
        }
      }
      if (index > -1) {
        this.posts[index] = post;
      } else {
        this.posts.push(post);
      }
    }
  
  }

    // Add or update conversaion for real-time sync of unreadMessagesCount.
    addOrUpdateConversation(conversation) {
      if (!this.conversationList) {
        this.conversationList = [conversation];
      } else {
        var index = -1;
        for (var i = 0; i < this.conversationList.length; i++) {
          if (this.conversationList[i].$key == conversation.$key) {
            index = i;
          }
        }
        if (index > -1) {
          this.conversationList[index] = conversation;
        } else {
          this.conversationList.push(conversation);
        }
      }
      this.computeUnreadMessagesCount();
    }
  
    // Compute all conversation's unreadMessages.
    computeUnreadMessagesCount() {
      this.unreadMessagesCount = 0;
      if (this.conversationList) {
        for (var i = 0; i < this.conversationList.length; i++) {
          this.unreadMessagesCount += this.conversationList[i].messages.length - this.conversationsInfo[i].messagesRead;
          if (this.unreadMessagesCount == 0) {
            this.unreadMessagesCount = null;
          }
        }
      }
    }

    getUnreadMessagesCount() {
      if (this.unreadMessagesCount) {
        if (this.unreadMessagesCount > 0) {
          return this.unreadMessagesCount;
        }
      }
      return null;
    }

    showGroupOptions() {
      const action = this.actionSheet.create({
        header: 'Create a new ...',
        backdropDismiss: true,
        mode: 'md',
        cssClass: 'GroupAction',
        buttons: [{
          text: 'Post',
          icon: 'chatbubbles-outline',
          cssClass: 'actionicon',
          handler: () => {
            this.newPost();
          }
        }, {
          text: 'Resource',
          icon: 'document-outline',
          handler: () => {
            this.newResource();
          }
        }, {
          text: 'Poll',
          icon: 'podium-outline',
          handler: () => {
            this.newPoll();
          }
        }, {
          text: 'Event',
          icon: 'calendar-outline',
          cssClass: 'cancelAction',
          handler: () => {
            this.newEvent();
          }
        }]
      }).then(r => r.present());
      }

  newPoll() {
    this.router.navigateByUrl('/new-poll/' + this.groupId);
  }

  newPost() {
    this.router.navigateByUrl('/new-post/' + this.groupId);
  }

  newResource() {
    this.router.navigateByUrl('/new-resource/' + this.groupId);
  }

  newEvent() {
    this.router.navigateByUrl('/new-event/' + this.groupId);
  }
}
